<template>
  <div>
    <h1>Tic Tac Toe</h1>
    <table id="table">
      <tr v-for="(row, rowsIndex) in states">
        <td v-for="(state, colsIndex) in row" @click="onSelect(rowsIndex, colsIndex)">
          <div style="color:blue;" v-if="state === 1">üîµ</div>
          <div style="color:red;" v-if="state === -1">üî¥</div>
        </td>
      </tr>
    </table>
    <div style="text-align:center;">
      <div style="color:blue;" v-if="playerId === 1">„Äåüîµ„Éó„É¨„Ç§„É§„Éº„Åï„Çì„ÄÅ„Éû„Çπ„ÇíÈÅ∏„Çì„Åß„Åè„Å†„Åï„ÅÑ„Äç</div>
      <div style="color:red;" v-if="playerId === -1">„Äåüî¥„Éó„É¨„Ç§„É§„Éº„Åï„Çì„ÄÅ„Éû„Çπ„ÇíÈÅ∏„Çì„Åß„Åè„Å†„Åï„ÅÑ„Äç</div>
    </div>
  </div>
</template>

<script>

  export default {
    name: "TicTacToe",
    data() {
      const length = this.property.magnification * 3;
      let states = new Array(length);
      this.initStates(states, length);

      return {
        length: length,
        states: states,
        playerId: 1,
        winnerId: 0,
        toastedOptions: {
          duration: 1000,
          position: "top-center",
          className: toasted
        }
      }
    },
    methods: {
      initStates(states) {
        for (let i = 0; i < states.length; i++) {
          const row = new Array(states.length).fill(0);
          states.splice(i, 1, row);
        }
      },
      onSelect(rowsIndex, colsIndex) {
        let state = this.states[rowsIndex][colsIndex];

        if(state !== 0) {
          this.$toasted.error('„Åì„ÅÆ„Éû„Çπ„ÅØÊó¢„Å´ÈÅ∏Êäû„Åï„Çå„Å¶„ÅÑ„Åæ„Åô!', this.toastedOptions);
          return
        }

        // ÈÅ∏Êäû„Åï„Çå„Åü„Éû„Çπ„ÇíÁèæÂú®„ÅÆ„Éó„É¨„Ç§„É§„ÉºID„Å´ÁΩÆÊèõ„Åô„Çã„ÄÇVue.js„Å´„É™„Ç¢„ÇØ„ÉÜ„Ç£„Éñ„Åï„Åõ„ÇãÁÇ∫„Å´splice„Çí‰ΩøÁî®„Åô„Çã„ÄÇ
        const row = this.states[rowsIndex];
        row[colsIndex] = this.playerId;
        this.states.splice(rowsIndex, 1, row);
        this.playerId *= -1;

        const winnerId = this.getWinnerId();
        if (winnerId !== 0) {
          this.initStates(this.states, this.length);
          let playerIds = {
            1: 'üîµ',
            '-1': 'üî¥'
          };
          this.$toasted.success(`${playerIds[winnerId]}„Åï„Çì„ÅÆÂãù„Å°„Åß„Åô„ÄÇ„Åä„ÇÅ„Åß„Å®„ÅÜ„Åî„Åñ„ÅÑ„Åæ„ÅôÔºÅ`, this.toastedOptions);
        } else if (this.isDraw()) {
          this.initStates(this.states, this.length);
          this.$toasted.success('Âºï„ÅçÂàÜ„Åë„Åß„ÅôÔºÅ', this.toastedOptions);
        }
      },
      getWinnerId() {
        for(let i = 0; i < this.length; i++){
          // Ê®™„ÅÆÂà§ÂÆö
          const row = this.states[i];
          if(this.isLineFilled(row)) {
            return row[0];
          }

          // Á∏¶„ÅÆÂà§ÂÆö
          const col = new Array(this.length);
          for(let j = 0; j < this.length; j++) {
            col[j] = this.states[j][i];
          }
          if(this.isLineFilled(col)) {
            return col[0];
          }
        }

        // Êñú„ÇÅ„ÅÆÂà§ÂÆö1
        const skew1 = new Array(this.length);
        for(let i = 0; i < this.length; i++) {
          skew1[i] = this.states[i][i];
        }
        if(this.isLineFilled(skew1)) {
          return skew1[0];
        }

        // Êñú„ÇÅ„ÅÆÂà§ÂÆö2
        console.log(this.states);
        const skew2 = new Array(this.length);
        for(let i = 0; i < this.length; i++) {
          skew2[i] = this.states[i][this.length - i - 1]
        }
        // console.log(`skew2: ${skew2}`);
        if(this.isLineFilled(skew2)) {
          return skew2[0];
        }

        // ÂãùÊïó„ÅåÊ±∫ÂÆö„Åó„Å¶„ÅÑ„Å™„ÅÑ
        return 0;
      },
      isLineFilled(line) {
        // „É©„Ç§„É≥Ë¶ÅÁ¥†„Å´1„Å§„Åß„ÇÇPlayerId=0(„Éá„Éï„Ç©„É´„ÉàÂÄ§Ôºâ„Åå„ÅÇ„Çå„Å∞ÂãùÊïó„ÅØÊ±∫„Åæ„Å£„Å¶„ÅÑ„Å™„ÅÑ
        for (const i of line) {
          if (i === 0) {
            return false;
          }
        }
        // „É©„Ç§„É≥Ë¶ÅÁ¥†„ÅåÂÖ®„Å¶Âêå„ÅòPlayerId„Å™„ÇâÂãùÊïó„ÅåÊ±∫„Åæ„Å£„Å¶„ÅÑ„Çã
        if (line[0] === line[1] && line[1] === line[2]) {
          return true;
        }
      },
      isDraw() {
        for (let i = 0; i < this.length; i++) {
          for (let j = 0; j < this.length; j++) {
            const state = this.states[i][j];
            // „Éá„Éï„Ç©„É´„ÉàÂÄ§„ÅåÂ≠òÂú®„Åô„Çã„ÅÆ„ÅßÂãùÊïó„ÅØÊ±∫„Åó„Å¶„ÅÑ„Å™„ÅÑ
            if (state === 0) {
              return false
            }
          }
        }
        // „Éá„Éï„Ç©„É´„ÉàÂÄ§„ÅåÂ≠òÂú®„Åó„Å™„ÅÑ„ÅÆ„Åß„ÄÅÂºï„ÅçÂàÜ„Åë
        return true
      },
    },
    props: {
      property: {
        magnification: {
          type: Number,
          default: 1
        }
      }
    }
  }
</script>

<style scoped>
  #table {
    margin: 0 auto;
    border-collapse: collapse;
    border: 3px solid #ccc;
  }

  #table td {
    border: 1px solid #ccc;
    height: 100px;
    width: 100px;
    text-align: center;
    vertical-align: middle;
    font-size: 50px;
    cursor: pointer;
  }

  h1 {
    text-shadow: 2px 2px 2px gray;
  }
  
  .toasted {
    font-family: "Noto Mono",serif;
  }

</style>
